import { EventEmitter } from 'events'

class App extends EventEmitter {
  constructor (opt = {}) {
    super()

    this.prevTime = window.performance.now()
    this.renderer = new THREE.WebGLRenderer({ antialias: true })
    this.renderer.setPixelRatio(window.devicePixelRatio)
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.renderer.vr.enabled = true
    document.body.appendChild(this.renderer.domElement)

    // WebXR
    document.body.appendChild(THREE.WEBVR.createButton(this.renderer, { referenceSpaceType: 'local' }))

    this.bgColor = 0x111111

    this.scene = new THREE.Scene()
    this.scene.background = new THREE.Color(this.bgColor)
    this.scene.fog = new THREE.Fog(this.bgColor, 0, 45)

    this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000)
    this.camera.layers.enable(1)

    window.addEventListener('resize', () => this.resize())

    this.animate()
  }

  resize () {
    this.camera.aspect = window.innerWidth / window.innerHeight
    this.camera.updateProjectionMatrix()

    this.renderer.setSize(window.innerWidth, window.innerHeight)
  }

  animate () {
    this.renderer.setAnimationLoop(this.render)
  }

  render () {
    const time = window.performance.now()
    const delta = (time - this.prevTime) / 1000
    this.prevTime = time

    this.update(delta, time)

    this.renderer.render(this.scene, this.camera)
  }

  update (dt = 0, time = 0) {
    console.log(dt, time)
    // recursively tell all child objects to update
    this.scene.traverse(obj => {
      if (typeof obj.update === 'function') {
        obj.update(dt, time)
      }
    })

    return this
  }
}

export default new App()
